'use client';

import React, { useState, useEffect } from 'react';
import { Box, Container } from '@mui/material';
import { Header } from '@/widgets/Header';
import { Navigation } from '@/widgets/Navigation';
import { User } from '@/entities/user/model/types';

interface AppLayoutProps {
  children: React.ReactNode;
}

export const AppLayout: React.FC<AppLayoutProps> = ({ children }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState<User | null>(null);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    // Mark component as mounted to prevent hydration mismatch
    setMounted(true);

    // Check for existing authentication state
    // This would typically check for a valid token in localStorage or cookies
    const token = localStorage.getItem('auth-token');
    if (token) {
      // Here you would validate the token and set authentication state
      // For now, we'll just simulate it
      setIsAuthenticated(true);
      setUser({
        id: '1',
        name: 'John Doe',
        email: 'john@example.com',
        avatar: undefined,
        role: 'user',
        isActive: true,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      });
    }
  }, []);

  const handleMenuToggle = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    setUser(null);
    localStorage.removeItem('auth-token');
    // Here you would typically call your logout API
    console.log('User logged out');
  };

  const handleLogin = () => {
    // This would be handled by your login page
    // For now, we'll just simulate authentication
    setIsAuthenticated(true);
    setUser({
      id: '1',
      name: 'John Doe',
      email: 'john@example.com',
      avatar: undefined,
      role: 'user',
      isActive: true,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    });
    localStorage.setItem('auth-token', 'dummy-token');
  };

  // Prevent hydration mismatch by using consistent initial state
  const displayIsAuthenticated = mounted ? isAuthenticated : false;
  const displayUser = mounted ? user : null;

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <Header
        onMenuToggle={handleMenuToggle}
        user={displayUser || undefined}
        onLogout={handleLogout}
      />

      <Box sx={{ display: 'flex', flex: 1 }}>
        <Box
          component="aside"
          sx={{
            width: isMenuOpen ? 240 : 0,
            transition: 'width 0.3s ease',
            overflow: 'hidden',
            borderRight: 1,
            borderColor: 'divider',
          }}
        >
          <Navigation
            isAuthenticated={displayIsAuthenticated}
            onLogout={handleLogout}
          />
        </Box>

        <Box component="main" sx={{ flex: 1, p: 3 }}>
          {children}
        </Box>
      </Box>
    </Box>
  );
};
