'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Divider,
  Box,
} from '@mui/material';
import { getPublicRoutes, getProtectedRoutes } from '@/shared/config/routes';
import { Button } from '@/shared/ui/Button';
import { getNavigationIcon } from '@/shared/lib/navigationUtils';

interface NavigationProps {
  isAuthenticated?: boolean;
  onLogout?: () => void;
}

export const Navigation: React.FC<NavigationProps> = ({
  isAuthenticated = false,
  onLogout,
}) => {
  const pathname = usePathname();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    // Mark component as mounted to prevent hydration mismatch
    setMounted(true);
  }, []);

  // Use consistent state during hydration
  const displayIsAuthenticated = mounted ? isAuthenticated : false;

  const navigationItems = displayIsAuthenticated
    ? getProtectedRoutes()
    : getPublicRoutes();

  return (
    <Box sx={{ width: 240, p: 2 }}>
      <List>
        {navigationItems.map((route) => (
          <ListItem key={route.path} disablePadding>
            <ListItemButton
              component={Link}
              href={route.path}
              selected={mounted && pathname === route.path}
              sx={{
                borderRadius: 1,
                mb: 0.5,
              }}
            >
              <ListItemIcon>{getNavigationIcon(route.path)}</ListItemIcon>
              <ListItemText primary={route.title} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>

      <Divider sx={{ my: 2 }} />

      <Box sx={{ p: 1 }}>
        {displayIsAuthenticated ? (
          <Button variant="outlined" size="small" onClick={onLogout} fullWidth>
            Logout
          </Button>
        ) : (
          <Link href="/login" style={{ textDecoration: 'none' }}>
            <Button variant="contained" size="small" fullWidth>
              Sign In
            </Button>
          </Link>
        )}
      </Box>
    </Box>
  );
};
